import React from "react"
import {ReactComponent as MailLogo} from "./img/at-solid.svg"

function About(props){
    return(
        <div className="fullpage">
            <div className="about" ref={props.myRef}>
                <div className="about_content">
                    <h2>Om mig</h2>
                    <br/>
                    <p>Jag är en kille från Borås som söker nya utmaningar.</p>
                    <p>Programmering är ett intresse som legat mig varmt om hjärtat ända sedan jag kom i kontakt med Basic i en grundkurs på gymnasiet. Nästa steg blev en fortsättningskurs i Visual Basic, sedan dess har jag varit fast.</p>
                    <p>Jag började utveckla mindre program på fritiden, bland annat ett program för att träna glosor till mina småsyskon. Under åren har jag använt mig av diverse olika tekniker, gick från Visual Basic till C++ där jag utvecklade ett par spel.</p>
                    <p>Resan fortsatte i C# där jag utvecklade första prototypen av min mobilapp Schemakollen, för att släppa den till mobil skaffade jag mig kunskaper i Java och registrerade mig på Google Play. Senare tid har jag intresserat mig för webb och då ramlat in på React, har bland annat skapat en webshop i vilken man kan checka ut med PayPal.</p>
                    <br/>
                    <p>Tveka inte att höra av er om ni har några frågor.</p>
                    <br/>
                    <div className="maillink_bottom">
                        <a tooltip="Skicka ett e-mail" href="mailto:kristofer@odehammar.com">
                            <MailLogo className="logolink logolink_bottom"/>
                        </a>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default About