import React from "react"

import Navbar from "./components/Navbar"
import Home from "./components/Home"
import Projects from "./components/Projects"
import About from "./components/About"
import Contact from "./components/Contact"
import BgComputer from "./components/img/bgcomputer.jpg"

class App extends React.Component{
    constructor(){
        super()
        this.state={
            
        }
        this.scrollToPart = this.scrollToPart.bind(this)
    
        this.homeRef= React.createRef()
        this.projRef= React.createRef()
        this.aboutRef= React.createRef()
        this.contactRef= React.createRef()
    }

    scrollToPart(id){
        if(id==="home"){
            this.homeRef.current.scrollIntoView({behavior: "smooth"})
        }else if(id==="projects"){
            this.projRef.current.scrollIntoView({behavior: "smooth"})
        }else if(id==="about"){
            this.aboutRef.current.scrollIntoView({behavior: "smooth"})
        }
    }

    render(){
        const navNames = [
            {name: "Hem", link: "home"},
            {name: "Mina projekt", link: "projects"},
            {name: "Om mig", link: "about"}
        ]

        return(
            <div className="maindiv">
                <img src={BgComputer} className="bgImage" alt=""/>
                <Navbar names={navNames} scrollToPart={this.scrollToPart}/>
                <Home myRef={this.homeRef} />
                <Projects myRef={this.projRef}/>
                <About myRef={this.aboutRef} />
                {/*<Contact myRef={this.contactRef} />*/}
            </div>
        )
    }
}

export default App