import React, { useState } from "react"
import Navitem from "./Navitem"

function Navbar(props){
    const navitems = props.names.map( (item) => <Navitem key={item.name} name={item} scrollToPart={props.scrollToPart} /> )
    const [navLinks, setNavLinks] = useState(false)
    const [navClass, setNavClass] = useState("nav_in")

    const toggleNavLinks=()=>{
        if(navLinks)
            setNavClass("nav_out")
        else
            setNavClass("nav_in")
        setNavLinks(!navLinks)
    }

    return(
        <nav className="navbar">
            <div className="navbarTitle">
                <p>Portfolio</p>
            </div>
            <div className={navClass}>
                <div onClick={toggleNavLinks} className="navbarlinks">
                    {navitems}
                </div>
                <div className="menu_hamburger" onClick={toggleNavLinks}>
                    <div className="menu_hamburger_1"></div>
                    <div className="menu_hamburger_2"></div>
                    <div className="menu_hamburger_3"></div>
                </div>
            </div>
        </nav>
    )
}

export default Navbar