import React from "react"
import "./styles.css"

import {ReactComponent as GitLogo} from "./img/github-brands.svg"
import {ReactComponent as LinkedinLogo} from "./img/linkedin-brands.svg"
import {ReactComponent as MailLogo} from "./img/at-solid.svg"


function Home(props){
    return(
        <div className="fullpage">
            <div className="home" ref={props.myRef} >
                <h3>Kristofer Odehammar</h3>
                <div className="titel">
                    <h4 className="titel_1">Front end-utvecklare</h4>
                    <h4 className="titel_2">Webutvecklare</h4>
                    <h4 className="titel_3">Programmerare</h4>
                </div>
                <a tooltip="Min Github" href="https://github.com/Hotheda">
                    <GitLogo className="logolink"/>
                </a>
                <a tooltip="Titta in på LinkedIn" href="http://www.linkedin.com/in/kristofer-odehammar-868a60177/">
                    <LinkedinLogo  className="logolink"/>
                </a>
                <a tooltip="Skicka ett e-mail" href="mailto:kristofer@odehammar.com">
                    <MailLogo className="logolink"/>
                </a>                
            </div>
        </div>
    )
}

export default Home